:root {
  --main-bg-color: white;
  --main-text-color: black;
  --main-color: #05bd39;
  --delete: #ff6d60;
}
/*navbar*/
.navbar-container {
  background-color: var(--main-bg-color);
  margin: 0;
  padding: 0;
  border-bottom: 3px solid var(--main-color);
}
.navbar-link {
  color: var(--main-text-color);
  text-decoration: none;
}
.home {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 100px;
}
.pages {
  max-width: 1400px;
  padding: 1rem;
}
/*home display*/
.workout-details {
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 1.2fr 1fr 1fr 1fr 0.1fr;
  justify-content: center;
  align-items: center;
  background-color: var(--main-bg-color);
  border-radius: 4px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.workout-details h4 {
  font-size: 1.2em;
  color: var(--main-color);
}
.workout-details p {
  margin: 0;
  font-size: 0.9em;
  color: var(--main-text-color);
}
.workout-details span {
  margin-right: 1rem;
  cursor: pointer;
  padding: 10px;
  border: 2px solid var(--delete);
  border-radius: 50%;
}
/*form*/
form h3 {
  color: var(--main-color);
}
.create-workout {
  padding: 3rem;
  margin-right: 10rem;
}

input,
label {
  display: block;
}
/*error*/
input {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}
.form-submit-btn {
  background: var(--main-color);
  border: 0;
  color: white;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}
div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid var(--delete);
  color: var(--delete);
  border-radius: 4px;
  margin: 20px 0;
}
input.error {
  border: 1px solid var(--delete);
}
